body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%), radial-gradient(at top center, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.40) 120%) #989898;
 background-blend-mode: multiply,multiply;
  }
  
  .App {
    font-family: sans-serif;
    text-align: center;
  }
  
  button {
    padding: 20px 60px;
    border: none;
    background-color: #333;
    color: aliceblue;
    font-size: 25px;
    cursor: pointer;
  }
  
  .square {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border: 1px solid black;
    margin: 5px;
  }
  
  .square span {
    visibility: hidden;
  }
  
  .letterbox {
    display: flex;
    flex-wrap: wrap;
    border-top: 5px solid #000;
    padding: 20px;

  }
  
  input {
    height: 70px;
    width: 70px;
    margin: 20px;
    font-size: 35px;
  }
  
  .square[visible="visible"] {
    background-color: green;
    animation: anime 1s;
  }
  
  .square[visible="visible"] span {
    visibility: visible;
  }

  .winText {
    position: fixed;
    height: 100px;
    top: -100px;
    right: 0;
    left: 0;
    background-color: rgb(42, 42, 42);
    color: whitesmoke;
    font-size: 40px;
    line-height: 100px;
    transition: top 1s;

  }
  .winText.active {
    top: 0;
  }

  

  @keyframes anime {
    from {
      transform: scale(150%);
    }
    to {
      transform: scale(100%);
    }
  }